.about {
  margin: 30px 0;
  padding: 20px;
  margin-left: 220px;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}

.about-img img {
  border-radius: 10px;
  width: 100%;
  /* height: 500px; */
  aspect-ratio: 1/1;
}

.about-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-content h1 {
  color: gray;
  font-family: cursive;
  border-bottom: 2px solid gray;
  text-align: center;
}
.about-content p {
  text-align: justify;
  padding: 20px;
}

/* DARK MODE */

#dark .about-content h1 {
  color: white;
}

#dark .about-content p {
  color: #ffffff;
}

/*Media  Query */

@media (max-width: 600px) {
  .about {
    margin: 0;
    padding: 0;
    margin-top: 20px;
    width: 100%;
  }

  .about-content {
    margin-top: 10px;
  }
}
