.work {
  background-color: #1387812a;
  margin-left: 140px;
  margin-top: 50px;
}

.work-exp {
  padding: 40px 0px;
}
.work-exp hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.work-exp h2 {
  font-weight: bold;

  color: #138781;
}

/* DARK MODE */

#dark .work {
  background-color: rgba(128, 128, 128, 0.101);
}

#dark .work h3 {
  color: white;
}

#dark .work h5 {
  color: white;
}
#dark .work ul {
  color: white;
}

/* Media Query */

@media (max-width: 600px) {
  .work {
    margin: 0px;
    padding: 0px;
    margin-top: 50px;
  }

  .work hr {
    width: 300px;
  }
}
