.education {
  padding: 100px 0px;
  margin-left: 140px;
  margin-top: 60px;
  background-color: #1387812a;
}
.education hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.education h2 {
  font-weight: bold;

  color: #138781;
}

/* DARK MODE */

#dark .vertical-timeline-element--work .vertical-timeline-element-date {
  color: white;
}

#dark .education p {
  color: black;
}

/* Media Query */

@media (max-width: 600px) {
  .education {
    margin: 0;
    padding: 0;
    margin-top: 40px;
  }

  .education h2 {
    padding-top: 20px;
  }

  .education hr {
    width: 300px;
    margin-bottom: 10px;
  }
}
