.sidebar-section {
  display: flex;
  background-color: black;
}

.sidebar {
  height: 100vh;
  background-color: #138781;
  width: 90px;
  position: fixed;
}

.sidebar-toggle {
  width: 200px;
}
.sidebar-toggle-icons {
  display: flex;
  justify-content: right;
  cursor: pointer;
}
.sidebar-toggle-icons p svg {
  background-color: #f29f67;
  margin-top: 30px;
  color: white;
}

/* MOBILE VIEW */

@media (max-width: 600px) {
  .sidebar {
    display: none;
  }
}
