.navbar-profile-pic {
  display: flex;
  border-radius: 90px;
  vertical-align: middle;
  margin: 0 auto;
}

.navbar-profile-pic img {
  height: 150px;
  width: 150px;
  margin-left: 28px;
  border-radius: 50%;
  border: 10px solid white;
}

.nav-item {
  color: white;
  margin-top: 30px;
  margin-left: 30px;
}

.nav-links {
  margin-top: 20px;
  cursor: pointer;
}
.nav-links svg {
  margin-right: 10px;
}

.nav-links:hover {
  color: #f29f67;
}

.nav-links .active {
  color: #f29f67;
  border-left: 5px solid #f29f67;
  padding-left: 10px;
  text-decoration: none;
}
