.home-container {
  height: 300px;
  color: white;
  padding-left: 220px;
}

.home-content h2 {
  padding-top: 70px;
  /* margin-left: 120px; */
}
.home-content h1 {
  color: #e8860e;
  /* margin-left: 120px; */
}
.home-buttons {
  /* margin-left: 120px; */
  padding: 5px;
  margin-top: 20px;
}
.home-buttons .btn-Hire {
  background-color: #3b8ff3;
  color: white;
  width: 160px;
}

.home-buttons .btn-Hire:hover {
  background-color: white;
  color: #3b8ff3;
  width: 180px;
  transition: 1s;
}

.home-buttons .btn-cv {
  margin-left: 20px;
  background-color: #3b8ff3;
  color: white;
  width: 160px;
}
.home-buttons .btn-cv:hover {
  transition: 1s;
  opacity: 80%;
}

.theme-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  background-color: #1e1e1e;
  margin-top: 20px;
  text-align: center;
  position: fixed;
  right: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  cursor: pointer;
  z-index: 1;
}

/* Media Queries */

@media (max-width: 600px) {
  .home-container {
    padding: 0;
    margin: 0;
    margin-top: 50px;
  }
}
.home-container h2 {
  font-size: 24px;
}

.home-container h1 {
  font-size: 24px;
}
