.projects {
  padding-left: 130px;
  margin-top: 60px;
}
.projects hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}

.projects h2 {
  font-weight: bold;

  color: #138781;
}

/* DARK MODE */
#dark .container p {
  color: white;
}

/* Media Query */

@media (max-width: 600px) {
  .projects {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }

  .projects hr {
    width: 300px;
  }
}
